<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>客户管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">预收冲应收</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看预收冲应收</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" class="demo-form-inline" :model="formInline">
        <el-form-item label="财务组织：" size="mini">
          {{formInline.company_name}}
        </el-form-item>
        <el-form-item label="单据号：" size="mini">
          {{formInline.deposits_sn}}
        </el-form-item>
        <el-form-item label="业务日期：" size="mini">
          {{formInline.business_time}}
        </el-form-item>
        <el-form-item label="客户名称：" size="mini">
          {{formInline.customer_name}}
        </el-form-item>
        <el-form-item label="部门：" size="mini">
          {{formInline.department_name}}
        </el-form-item>
        <el-form-item label="业务员：" size="mini">
          {{formInline.business_name}}
        </el-form-item>
        <el-form-item label="备注：" size="mini">
          {{formInline.remark}}
        </el-form-item>
        <el-form-item label="审核状态：" size="mini">
          {{formInline.auditor_status_name}}
        </el-form-item>
      </el-form>
    </div>
    <div class="paymanageTitle">
      <i></i> <span>结算明细</span>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.receipt_common" style="width: 100%" :summary-method="getSummaries" show-summary max-height="600">
        <el-table-column :show-overflow-tooltip="true" type="index" width="46" label="序号" align="center"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="receivables_mode_name" label="结算方式">
          <template slot-scope="scope">
            预收冲应收
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="bank_name" label="银行帐户">
          <template slot-scope="scope">
            预收账户
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="common_price" label="金额" align="right">
          <template slot-scope="scope">
            <el-input v-model="scope.row.common_price" class="editor" :disabled="true"></el-input>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
          <template slot-scope="scope">
            <span @click="searchRows(scope, scope.row)" style="cursor: pointer">
              {{scope.row.remark}}
            </span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="aboutList" v-if="is_disabled">
      <div class="paymanageTitle">
        <i></i> <span>核销明细</span>
      </div>
      <div class="table_container">
        <div>
          <el-table :highlight-current-row="true" tooltip-effect="light" border :data="relationTable.tableData" show-summary :summary-method="getrelationTableSummaries" max-height="600">
            <el-table-column :show-overflow-tooltip="true" label="序号" width="46" align="center">
              <template slot-scope="scope">
                {{scope.$index+1}}
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="bill_sn" label="单据号" min-width="120"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="order_type" label="单据类型"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="customer_name" label="客户名称"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="bill_num" label="出库数量" align="right"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="order_price" label="出库金额" align="right">
              <template slot-scope="scope">
                <pre>{{scope.row.order_price  | priceFormatNone}}</pre>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="ec_channel_fee" label="通道费" align="right">
              <template slot-scope="scope">
                <pre>{{scope.row.ec_channel_fee  | priceFormatNone}}</pre>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="sc_price" label="实际应收金额" align="right" :render-header="renderHeader">
              <template slot-scope="scope">
                <pre>{{scope.row.sc_price  | priceFormatNone}}</pre>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="add_time" label="单据时间" min-width="120"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="remark" label="备注" width="156"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="rc_price" label="已冲销金额" align="right">
              <template slot-scope="scope">
                <pre>{{scope.row.rc_price  | priceFormatNone}}</pre>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="rest_price" label="未冲销金额" align="right">
              <template slot-scope="scope">
                <pre>{{scope.row.rest_price  | priceFormatNone}}</pre>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="bill_price" label="本次冲销金额" align="right">
              <template slot-scope="scope">
                <pre>{{scope.row.bill_price  | priceFormatNone}}</pre>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="payment_method" label="回款方式" width="156"></el-table-column>
          </el-table>
        </div>

      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <!--未审核-->
      <template v-if="formInline.auditor_status==1">
        <el-button type="primary" @click="examineFun" v-if="authority.b_pass==1">审 核</el-button>
        <el-button @click="abolishFun" v-if="authority.b_invalid==1">作 废</el-button>
      </template>
      <el-button @click="nopass" type="primary" v-if="authority.b_nopass==1&&formInline.auditor_status==2">弃 审</el-button>
      <template v-if="formInline.auditor_status==3 || (authority.b_invalid!=1 && authority.b_pass!=1)">
        <el-button @click="goBack">返 回</el-button>
      </template>
      <template v-else>
        <el-button @click="goBack">取 消</el-button>
      </template>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "customeDepositsSearch",
  data() {
    return {
      goBackText: null,
      authority: {},
      formInline: {},
      TableData: {},
      //客户理赔
      isShow_sbList: false,
      sbListTabel: { tableData: [] },

      //    客户返利
      isShow_flList: false,
      flListTabel: { tableData: [] },

      //    销售出库
      isShow_xschukuList: false,
      xsListTabel: { tableData: [] },

      //判断是否从审核页查看
      receiptaudit: "",
      receipt_id: "",

      is_disabled: false,
      relationTable: {
        tableData: []
      },
      title: "",
      changeIndex: 0
    };
  },
  computed: {
    ...mapGetters(["depositsSearch_showGet"])
  },
  methods: {
    ...mapActions(["depositsIndexACT_show", "depositsIndexACT"]), // wz alter
    goBack() {
      this.depositsIndexACT({
        // this.depositsIndexACT_show({
        // commont: "depositsIndexACT",
        params: this.depositsSearch_showGet.params,
        goPage: this.depositsSearch_showGet.goPage,
        pageSize: this.depositsSearch_showGet.pageSize
      });
      this.$router.push({
        path: "/custom/deposits"
      });
    },
    renderHeader() {
      return (
        <el-tooltip class="item" effect="dark" placement="top">
          <p slot="content">实际应收金额=本出库单中出库金额</p>
          <span
            class="el-icon-question"
            style="cursor:pointer;font-weight:bold"
          >
            实际应收金额
          </span>
        </el-tooltip>
      );
    },
    changeTitle(index) {
      this.changeIndex = index;
    },
    searchRows(scope, row) {
      let table = [];
      table.push({
        accounts_type_id: row.accounts_type_id,
        receivables_mode_id: row.receivables_mode_id,
        bank_id: row.bank_id,
        common_price: row.common_price,
        remark: row.remark == undefined ? "" : row.remark
      });
      let vm = this;
      this.formInline.business_time = this.$publicFution.formatData(new Date());
      let funcan = {
        customer_id: this.formInline.customer_id,
        business_time: this.formInline.business_time,
        department_id: this.formInline.department_id,
        company_id: this.formInline.company_id,
        business_id: this.formInline.business_id,
        remark: this.formInline.remark,
        is_verify: 1,
        common_body: JSON.stringify(table)
      };
      vm.$axios({
        method: "post",
        url: vm.$api.addReceipt,
        data: funcan
      }).then(res => {
        if (res.data.data.length == 0) {
          this.is_disabled = false;
        } else {
          this.is_disabled = true;
          this.relationTable.tableData = res.data.data[0].list;
          this.title = res.data.data[0].title;
        }
      });
    },
    examineFun() {
      //审核
      this.$axios({
        method: "post",
        url: this.$api.auditorPassdeposits,
        data: {
          deposits_id: this.depositsSearch_showGet.deposits_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    nopass() {
      this.$axios({
        method: "post",
        url: this.$api.noauditorPassdeposits,
        data: {
          deposits_id: this.depositsSearch_showGet.deposits_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },

    abolishFun() {
      //作废
      this.$axios({
        method: "post",
        url: this.$api.auditorInvaliddeposits,
        data: {
          deposits_id: this.depositsSearch_showGet.deposits_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "总计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 3) {
            sums[index] = parseFloat(sums[index]).toFixed(2);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    },
    getrelationTableSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "总计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 4) {
            sums[index] = sums[index];
          } else if ([5, 6, 7, 13, 11, 12].includes(index)) {
            sums[index] = parseFloat(sums[index]).toFixed(2);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    }
  },
  computed: {
    ...mapGetters(["depositsSearch_showGet"])
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    let vm = this;
    this.$axios({
      method: "get",
      url: this.$api.getDepositsInfo,
      params: {
        deposits_id: this.depositsSearch_showGet.deposits_id,
        is_edit: 0
      }
    }).then(({ data }) => {
      if (data.code == 200) {
        this.$set(this, "TableData", data.data);
        this.formInline = this.TableData;

        this.receiptaudit = this.depositsSearch_showGet.receiptaudit;
        this.deposits_id = this.depositsSearch_showGet.deposits_id;
        this.formInline.auditor_status_name == "未审核"
          ? (this.goBackText = "取 消")
          : (this.goBackText = "返 回");

        this.TableData.receipt_common = data.data.deposits_common;
        if (
          data.data.receipt_common_bill.length == undefined ||
          data.data.receipt_common_bill.length == 0
        ) {
          this.is_disabled = false;
        } else {
          this.is_disabled = true;
          this.relationTable.tableData = data.data.receipt_common_bill;
        }
      }
    });
  }
};
</script>

